<!-- 
	This is the new user page, it uses the dashboard layout in: 
	"./layouts/Dashboard.vue" .
 -->

<template>
  <div class="mx-auto mt-50" style="max-width: 800px">

    <div class="mb-24">
      <a-card
        :bordered="false"
        class="header-solid"
        :bodyStyle="{ paddingTop: 0 }"
        :headStyle="{ paddingBottom: '0' }"
      >
        <template #title>
          <h5 class="mb-0">Administrator Information</h5>
          <p class="font-regular">Please enter required information here</p>
		  <template v-if="error_msg">
          <a-alert class="mt-10" :message="error_msg" type="error" closable />
        </template>
        </template>
        <a-form :form="form" @submit="handleSubmit" :hideRequiredMark="true">
          <a-row :gutter="[24]">
            <a-col :span="12">
              <a-form-item class="mb-10" label="Name" :colon="false">
                <a-input
                  v-decorator="[
                    'name',
                    {
                      rules: [{ type: 'string', required: true }],
                    },
                  ]"
                  placeholder="Enter your full name"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item class="mb-10" label="Username" :colon="false">
                <a-input
                  v-decorator="[
                    'username',
                    {
                      rules: [{ type: 'string', required: true }],
                    },
                  ]"
                  placeholder="Enter your username"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item class="mb-10" label="Phone" :colon="false">
                <a-input
                  v-decorator="[
                    'phone',
                    {
                      rules: [{ type: 'string', required: true }],
                    },
                  ]"
                  placeholder="Enter your phone number"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item class="mb-10" label="Email address" :colon="false">
                <a-input
                  v-decorator="[
                    'email',
                    {
                      rules: [{ type: 'email', required: true }],
                    },
                  ]"
                  placeholder="Enter your email address"
                />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item class="mb-10" label="Password" :colon="false">
                <a-input
            v-decorator="[
              'password',
              {
                rules: [
                  { initialValue: ``, required: true, message: 'Please input your password!' },
                ],
              },
            ]"
            type="password"
            placeholder="Enter your password"
          />
              </a-form-item>
            </a-col>
            <a-col :span="12">
              <a-form-item class="mb-10" label="Repeat Password" :colon="false">
               <a-input
            v-decorator="[
              'password_confirmation',
              {
                rules: [
                  { required: true, message: 'Please repeat your password!' },
                  { validator: validatePassword },
                ],
              },
            ]"
            type="password"
            placeholder="Enter your password again"
          />
              </a-form-item>
            </a-col>
          </a-row>
          <a-row :gutter="[24]">
            <a-col :span="12"> </a-col>
            <a-col :span="12" class="text-right">
              <a-button :loading="loading" type="primary" @click="handleSubmit()" class="px-25"
                >Submit</a-button
              >
            </a-col>
          </a-row>
        </a-form>
      </a-card>
    </div>
  </div>
</template>

<script>
  const axios = require('axios')

  export default {
      name:"edite_layout",
  components: {},
  data() {
    return {
      error_msg: "",
	  loading:false,
      
    };
  },
  beforeCreate() {
    // Creates the form and adds to it component's "form" property.
    this.form = this.$form.createForm(this, { name: "user" });
  },
  methods: {
    // Handles input validation after submission.
    validatePassword(rule, value, callback) {
      if (value && value !== this.form.getFieldValue("password")) {
        callback("Password & repeat password not same!");
      } else {
        callback();
      }
    },
    handleSubmit() {
      this.form.validateFields((err, values) => {
		this.error_msg=""
        if (!err) {
			this.loading=true

          axios.put(
             "/admin/manager/"+this.$route.params.id,
             values
          ).then(

            (response) => {
              console.log(response)

              this.$router.push("/admins");
            }).catch((error) => {
				console.log( error.response)
                this.loading = false;
                this.error_msg =
                  (error.response && error.response.data.message) ||
                  error.message;
                error.toString();
              });
        }
      });
    },
    fetch() {
		this.error_msg=""
			this.loading=true
          axios.get(
             "/admin/manager/"+this.$route.params.id
          ).then(
            (response) => {
             // this.form=response.data.data();
              this.form.password="";
              this.form.setFieldsValue(response.data.data);
              this.loading = false;

            }).catch((error) => {
				console.log( error.response)
                this.loading = false;
                this.error_msg =
                  (error.response && error.response.data.message) ||
                  error.message;
                error.toString();
              });
    },
  },
  mounted()
  {
   this.fetch();
  },
};
</script>

<style lang="scss">
</style>